<template>
  <div class="cc">
    <div class="row">
      <div class="col-2">
        <button class="btn btn-secondary btn-block m-1" v-on:click="handleNewModel">Create new model</button>
      </div>
      <div class="col-2">
        <button
          class="btn btn-secondary btn-block m-1"
          v-on:click="handleNewManager"
        >Create new Manager</button>
      </div>
      <div class="col-2">
        <button class="btn btn-secondary btn-block m-1" v-on:click="handleNewJob">Create new job</button>
      </div>
      <div class="col-2">
        <button class="btn btn-secondary btn-block m-1">Edit job</button>
      </div>
    </div>
    <div class="row">
      <NewJobForm v-show="show === 1"></NewJobForm>
      <NewManagerForm v-show="show === 2"></NewManagerForm>
      <NewModelForm v-show="show === 3"></NewModelForm>
    </div>
  </div>
</template>

<script>
import NewJobForm from "@/components/NewJobForm";
import NewManagerForm from "@/components/NewManagerForm";
import NewModelForm from "@/components/NewModelForm";
export default {
  data() {
    return {
      show: 0
    };
  },
  components: {
    NewJobForm,
    NewManagerForm,
    NewModelForm
  },
  methods: {
    handleNewJob() {
      this.show = 1;
    },
    handleNewManager() {
      this.show = 2;
    },
    handleNewModel() {
      this.show = 3;
    }
  }
};
</script>

<style scoped>
.cc {
  padding: 5px;
}
</style>