<template>
  <div class="cc">
    <span v-show="errors.length">
      <b>Required:</b>
      <ul>
        <li v-for="error in errors" :key="error.message">{{error.message}}</li>
      </ul>
    </span>
    <form @submit.prevent="checkForm">
      <div class="form-group">
        <label for="firstName">First name</label>
        <input class="form-control" v-model="firstName" name="firstName" />
      </div>
      <div class="form-group">
        <label for="lastName">Last name</label>
        <input class="form-control" v-model="lastName" name="lastName" />
      </div>
      <div class="form-group">
        <label for="email">Email</label>
        <input class="form-control" v-model="email" name="email" />
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input class="form-control" v-model="password" name="password" type="password" />
      </div>
      <div class="formgroup">
        <input type="submit" value="Create new job" class="btn btn-success" />
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      errors: [],
      form: {
        firstName: null,
        lastName: null,
        email: null,
        password: null
      }
    };
  },
  methods: {
    checkForm(e) {
      if (this.firstName && this.lastName && this.email && this.password) {
        return true;
      }
      this.errors = [];
      if (!this.firstName) {
        this.errors.push({ message: "No first name" });
      }
      if (!this.lastName) {
        this.errors.push({ message: "No last name" });
      }
      if (!this.email) {
        this.errors.push({ message: "No email" });
      }
      if (!this.password) {
        this.errors.push({ message: "No password" });
      }

      e.PreventDefault();
    }
  }
};
</script>

<style scoped>
.cc {
  margin: 20px;
}
</style>